import './Catalog.scss';
import { Pagination } from '../Pagination/Pagination';
import { PageHeadingTwice } from '../PageHeading/PageHeading';
import { ProductCard } from '../Productard/Productard';
import { useState } from 'react';
import { Breadcrumbs } from '../Breadcrumbs/Breadcrumbs';
import { products } from '../../productsData';

export const Catalog = () => {

    const breadcrumbs = [
        {
            name: "",
            to: "/catalog"
        },
    ];

    const countProducts = products.length;

    // 
    //      
    const [currentPage, setCurrentPage] = useState(1);

    //        
    const [productsPerPage] = useState(12);

    //    5
    const lastProductIndex = currentPage * productsPerPage;

    //    0
    const firstProductIndex = lastProductIndex - productsPerPage;

    //            
    const currentProduct = products.slice(firstProductIndex, lastProductIndex);

    //      
    const paginate = pageNumber => setCurrentPage(pageNumber);

    return (
        <>
            <Breadcrumbs breadcrumbs={breadcrumbs} />
            <div className="catalog">
                <PageHeadingTwice>
                     
                </PageHeadingTwice>
                <div className="catalog-wrap catalog-wrap_top container">
                    <div className="result"> {countProducts} </div>
                </div>
                <div className="wrap-small">
                    <div className="result"> {countProducts} </div>
                </div>
                <div className="catalog-product-wrap">
                    <div className="catalog-product">
                        {currentProduct.map((product) => (
                            <ProductCard
                                key={product.id}
                                product={product}
                            />
                        ))}
                    </div>
                </div>
                <Pagination
                    productsPerPage={productsPerPage}
                    totalProducts={products.length}
                    paginate={paginate}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                />
            </div >
        </>
    )
}